import Listing from "Pages/MyListing/Components/Listing";
import React, { useCallback, useEffect } from "react";
import { Col, Row } from "reactstrap";
import { AuthContext } from "Boot/auth/AuthProvider";

const Favourites = ({
  getIndex,
  list,
  title,
  subTitle,
  description,
  ...props
}) => {

  const memoizedGetIndex = useCallback(getIndex, [getIndex]);

  useEffect(() => {
    memoizedGetIndex();
  }, [memoizedGetIndex]);

  return (
    <Col md="9">
      <div className="dashboardContent">

        <Row>
          <Listing
            list={list}
            title="My Favourite Listings"
            description="This is a list of your favourite properties."
            subTitle=""
            {...props}
          />
        </Row>
      </div>
    </Col>
  );
};
export default Favourites;
